*{
    margin: 0;
    padding: 0;
}
main{
    width: 100%;
    max-width: 960px;
    margin: 0 auto;
}
.searchbox{
    width: 100%;
    padding: 15px;
    border: none;
    outline: none;
    background: none;
    background-color: #EEE;
    border-radius: 8px;
    color: black;
    font-size: 18px;
    font-weight: 300;
    transition: 0.4s ease-out;



}
.searchbox:focus{
    box-shadow: 0px 0px 4px 8px pink;
}

.App{
    text-align: center;
    
    display: flex;
   flex-direction: row;
   flex-wrap: wrap;
   justify-content: space-around;
   margin: 20px 40px;
   
}
.card{
    width: 250px;
    height: 350px;
    box-shadow: 0  4px 8px 0 rgba(0, 0,0,0.2);
    transition: 0.3s;
 

    
    
}
.card:hover{
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}
img{
    height: 200px;
    margin-bottom: 10px;
}
.title{
    font-size: 18px;
    font-weight: 700;
    float: left;
}
.subtitle{
    font-size: 15px;
    float: left;
    
}
.nav{
    width: 100%;
}